@import url(https://fonts.googleapis.com/css?family=Codystar:300&display=swap);
* {
	padding: 0;
	margin: 0;
}

body {
	background-color: rgba(198, 219, 240, 0.897);
}

.cards {
	border-radius: 44px 58px;
	background-color: rgb(146, 137, 137);
	display: flex;
	flex-direction: column;
	justify-content: start;
	align-items: center;
	border: 2px solid red;
	width: 30vw;
	margin-top: 15px;
}
.card-img {
	width: 100%;
}
.card-img img {
	border-radius: 44px 58px 0px 0px;
	width: 100%;
	height: 40vh;
}

.card-detail {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
}
.card-info h2 {
	font-family: Georgia, sans-serif;
	text-align: center;
}

.button {
	width: 100px;
	padding: 6px;
	border-radius: 10px;
	font-size: 18px;
	text-align: center;
	background-color: #006989;
	color: white;
	margin-bottom: 10px;
}
.description {
	padding: 10px;
	text-align: center;
}
.button:hover {
	transform: translate(0%, -10%);
}

.button a {
	text-decoration: none;
	color: white;
}

h1 {
	display: flex;
	justify-content: center;
	margin: 20px;
	padding: 20px;
	color: white;
	text-shadow: 2px 2px silver;
}
.container {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	margin-bottom: 10px;
}

